.request {
  position: relative;

  display: flex;
  justify-content: space-between;
  align-items: center;

  margin-top: 80px;

  &::after {
    content: "";
    position: absolute;
    top: 80px;
    z-index: -1;

    width: 100%;
    height: 300px;

    background-color: $light-grey;
  }

  & .form__title {
    padding: 0;

    background-color: transparent;
  }

  & .form-price {
    margin-top: 20px;
  }
}

.request__form {
  width: 300px;
}

@media screen and (max-width: 991px) {
  .request {
    flex-wrap: wrap;
    justify-content: space-around;

    &::after {
      display: none;
    }
  }

  .request__form  {
    width: 400px;
    padding: 30px;
    background-color: $light-grey;
    margin-top: 40px;
  }
}

@media screen and (max-width: 767px) {
  .request {
    margin-top: 50px;

    &::after {
      display: none;
    }
  }

  .request__form {
    order: 3;
  }
}
